import React, { useState } from 'react';
//import logo from './logo.svg';
import ball from './Ball.gif'
import TvMan from './TvMan.gif'
//import { motion } from "framer-motion"
import Name from './NameAnimation'
import Typist from 'react-typist';
import './type.scss'
import {useSpring, animated, useTrail} from 'react-spring'

// Styled-components lets you write actual CSS in your JavaScript
import styled, { ThemeProvider } from 'styled-components'

import './App.css';


const themes = ["palevioletred", "mediumseagreen", "violet", "slateblue", "deepskyblue", "paleturquoise", "silver", "navajowhite"]

const Button = styled.button`
  background: transparent;
  border-radius: 3px;
  margin: 0 1em;
  font-size: 18px;
  padding: 0.25em 1em;
  cursor: pointer;

  /* Color the border and text with theme.main */
  color: ${props => props.theme.main};
  border: 2px solid ${props => props.theme.main};

  transition: 0.5s all ease-out;

  &:hover {
    background: solid;
    background-color: ${props => props.theme.main};
    border: 3px solid ${props => props.theme.main};
    color: white;
    box-shadow: 0 12px 16px 0 rgba(192,192,192,0.3), 0 17px 50px 0 rgba(192,192,192,0.1);
  }
`


// We are passing a default theme for Buttons that arent wrapped in the ThemeProvider
Button.defaultProps = {
  theme: {
    main: "palevioletred"
  }
}


const Container = styled.div`
  text-align: center;
  text-center: center;
  height: 60px;
`


function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}



const ballGifSize = '35px'

function App() {
  const props = useSpring({opacity: 1, from: {opacity: 0}})
  //const props2 = useSpring({ value: 100, from: { value: 0 } })

  const props2 = useSpring({
    to: async (next, cancel) => {
      await next({opacity: 0, color: 'rgb(14,26,19)'})
      await next({opacity: 0.2, color: '#ffaaee'})
      await next({opacity: 0.1, color: 'rgb(14,26,19)'})
      await next({opacity: 0.5, color: '#ffaaee'})
      await next({opacity: 0.3, color: 'rgb(14,26,19)'})
      await next({opacity: 0.9, color: '#ffaaee'})
      await next({opacity: 0.7, color: 'rgb(14,26,19)'})
      await next({opacity: 1, color: '#ffaaee'})
    },
    from: {opacity: 0, color: 'red'}
  })
  

  // Defining a theme for Styled components, Random every request.
  const theme = {
    main: themes[getRandomInt(themes.length)]
  }

//<img src={ball} alt="ball" height={ballGifSize} width={ballGifSize}/>


  return (
    <div className="App">
     
     <Name themeColor={theme.main}/>

      <animated.div style={props}>
        <ThemeProvider theme={theme}>
          <Container>
            <Button href=""> Projects </Button>
            <Button href=""> Articles </Button>
            <Button href=""> About </Button>
            <Button href=""> Contact </Button>
          </Container>
        
        </ThemeProvider>
      </animated.div>


      <animated.div style={props2}>
        <br></br>
        <img src={TvMan} alt="TvMan" height="382px" width="284px"/>
      </animated.div>

      <div>
              <Typist
                      cursor={{color:theme.main }}
                      avgTypingDelay={40}
                      startDelay={2000}
              >
                  <h1 style={{color: theme.main, font:'Lucida Console', width:'1000px', display:'inline'}}> 
                    Welcome to my portfolio
                  </h1>
                  
    
                 
              </Typist>
      </div>

    </div>
  );
}

export default App;
